body {
    margin: 0;
    box-sizing: border-box;
    color: white;
    background-color: rgb(100, 46, 7);
}

h1 {
    text-align: center;
}

.buttons {
    display: flex;
    justify-content: space-around;
}

button {
    padding: 8px 20px;
    border: 1px solid black;
    border-radius: 20px;
    cursor: pointer;
    transition: all .3s ease-in-out;
    color: black;
}

button:hover {
    color: white;
    background-color: black;
}

p {
    font-size: 150%;
    text-align: center;
}

